import React, { useRef, useState } from 'react'
import { Form, Button,Alert } from "react-bootstrap"
import {useAuth} from "../contexts/Auth"
import {Link, useHistory} from 'react-router-dom'
import '../App.css'
export default function Profile() { 
        const emailRef = useRef()    
        const {currentUser, updatePass} = useAuth()  
        const passwordRef = useRef()
        const [error, setError] = useState(' ')
        const [loading, setLoading] = useState(false)
        const {login} = useAuth()
        const history = useHistory()

        function submit_profile(e) {
        e.preventDefault()
        setLoading(true)
        setError('')
        const promises = []
        if (passwordRef.current.value) {
            promises.push(updatePass(passwordRef.current.value))
        }
        Promise.all(promises).then(() => {
            history.push('/Login')
        }).catch(() => 
            setError('Không thể cập nhật')
        ).finally(() => 
        setLoading(false)
        ) 
    }
        return ( 
        <div className='login'>
                {error && <Alert variant='danger'>{error}</Alert>}
                <Form className='form' onSubmit={submit_profile}>
                    <h3> Thông tin tài khoản </h3>
                    <div> Email: </div>
                    <span> {currentUser.email} </span>
                    <input  type="password"
                            placeholder="Mật khẩu mới"
                            ref={passwordRef}
                            ></input>
                    <Button className='btn_login' disabled={loading} type='submit'>Cập nhật</Button>
                </Form>
                <Link to='website'>
                    <p className='register-text2'>Quay trở lại trang chủ</p>
                </Link>
        </div>
         );
    }   